import React from 'react'
import { useAuth } from '../context/AppContext'

const Welcome = () => {

    const { currentUser } = useAuth()

    return (
        <div className='welcome'>
            <h3 className='mb-3'>¡Bienvenido <span className='text-info'>{currentUser.displayName}</span>!</h3>
            <h5>¡Estamos mejorando la dinamica de nuestra comunicación!</h5>
            <p className='mb-1'>A través de esta plataforma pedí diseños, publicaciones en redes, noticias web y hacenos llegar tus novedades para Somos.</p>
            <p>Además consulta los manuales de marca y contenidos web de GCBA.</p>
        </div>
    )
}

export default Welcome
