import React from 'react'
import { Link } from 'react-router-dom'
import { Container } from 'react-bootstrap'

const Atajos = () => {
    return (
        <section className="atajos">
            <Container className="container">
                <div className="row row-shortcut">
                    <Link className="col-md-3 col-sm-12 col-xs-12 shortcut" to='/diseno' >
                        <span className="bg-warning-lt">
                            <span className="fa fa-pencil glyphicon glyphicon-user"></span>
                        </span>
                        <h3>Diseños</h3>
                        <p>Promocioná la gestión de tu área</p>
                    </Link>
                    <Link className="col-md-3 col-sm-12 col-xs-12 shortcut" to='/redes' >
                        <span className="bg-blue-lt">
                            <span className="fa fa-facebook glyphicon glyphicon-list-alt"></span>
                        </span>
                        <h3>Publicación en Redes</h3>
                        <p>Compartí las novedades</p>
                    </Link>
                    <Link className="col-md-3 col-sm-12 col-xs-12 shortcut" to='/test' >
                        <span className="bg-violet-lt">
                            <span className="fa fa-newspaper-o glyphicon glyphicon-phone"></span>
                        </span>
                        <h3>Web GCBA</h3>
                        <p>Modificá y creá contenido</p>
                    </Link>
                    <Link className="col-md-3 col-sm-12 col-xs-12 shortcut" to='/testdiseno' >
                        <span className="bg-success-lt">
                            <span className="fa fa-users glyphicon glyphicon-tree-deciduous"></span>
                        </span>
                        <h3>Somos</h3>
                        <p>Comunicá internamente</p>
                    </Link>
                </div>
            </Container>
      </section>
    )
}

export default Atajos
