import React, { useRef, useState, forwardRef } from 'react'
import { useHistory, Link } from 'react-router-dom'
import { Container, Form, Row, Col, Button } from 'react-bootstrap'
import DatePicker from "react-datepicker";
import {subDays} from "date-fns";
import { useAuth } from '../context/AppContext'

const Redes = () => {

    const { pedidoRedesSubmited, currentUser } = useAuth()

    const history = useHistory()

    let sended = false
    const nameRef = useRef('')
    const emailRef = useRef('')
    const ccEmailRef = useRef('')
    const areaRef = useRef('')
    const programaRef = useRef('')

    const [ tipoDePublicacion, setTipodePublicacion ] = useState('')
    const onValueChange = (e) => {
        setTipodePublicacion(e.target.value)
    }

    const textoParaPublicacionRef = useRef('')
    const [startDate, setStartDate] = useState(new Date());
    const fechaDePublicacionRef = useRef('')
    const urlImagenesRef = useRef('')
    const observacionesRef = useRef('')

    const ExampleCustomInput = forwardRef(
        ({ value, onClick }, ref) => (
            <input 
                name='entry.1592263970' 
                className="btn btn-default ml-3" 
                onClick={(e)=>onClick()} 
                ref={fechaDePublicacionRef}
                defaultValue={value}
                
            />
        ),
    );

    return (
        <Container>
            <h1 className='mb-4'>Iniciá tu publicación en Redes</h1>
            <hr/>
            <iframe 
                name='hidden_iframe' 
                title='hidden_iframe'
                id='hidden_iframe' 
                style={{display:'none'}} 
                onLoad={()=>{
                    if (sended) {
                        const newPedido = {
                            id: currentUser.uid,
                            type: 'Diseño',
                            info: [
                                {text: 'Nombre: ',
                                answer: nameRef.current.value},
                                {text: 'Email: ',
                                answer: emailRef.current.value},
                                {text: 'Email para Copia:',
                                answer: ccEmailRef.current.value},
                                {text: 'Área: ',
                                answer: areaRef.current.value},
                                {text: 'Programa: ',
                                answer: programaRef.current.value},
                                {text: 'Tipo de publicación: ',
                                answer: tipoDePublicacion},
                                {text: 'Texto para la publicación',
                                answer: textoParaPublicacionRef.current.value},
                                {text: 'Fecha de publicación: ',
                                answer: fechaDePublicacionRef.current.value},
                                {text: 'Url para Imágenes: ',
                                answer: urlImagenesRef.current.value},
                                {text: 'Observaciones: ',
                                answer: observacionesRef.current.value},
                            ]
                        }
                        pedidoRedesSubmited(newPedido)
                        history.push('/todobien')
                    }
                }}
            />
            <Form
                action='https://docs.google.com/forms/u/1/d/e/1FAIpQLSdDPsKHq_ldc8ofxbKPrHekyo3ZjVPBad4O1oEO7N5doK43rQ/formResponse'
                onSubmit={ ()=> sended = true }
                target='hidden_iframe'
                method='POST'
                className='mb-5'
            >
                <Form.Group controlId="email">
                    <Form.Label>Tu nombre: </Form.Label>
                    <Form.Control 
                        ref={nameRef}
                        name='entry.1626081267' 
                        type="text" 
                        placeholder="Juan Vecinos" 
                    />
                </Form.Group>
                <Row>
                    <Col>
                        <Form.Group controlId="CCemail">
                            <Form.Label>Correo Electrónico: </Form.Label>
                            <Form.Control 
                                ref={emailRef}
                                name='entry.711776014' 
                                type="text" 
                                placeholder="tu.nombre@email.com" 
                            />
                        </Form.Group>
                    </Col>
                    <Col>
                        <Form.Group controlId="">
                            <Form.Label>Correo Electrónico en copia: </Form.Label>
                            <Form.Control 
                                ref={ccEmailRef}
                                name='entry.2041551645' 
                                type="text" 
                                placeholder="tu.nombre@email.com, otro.nombre@email.com" 
                            />
                        </Form.Group>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <Form.Group controlId="">
                            <Form.Label>Área: </Form.Label>
                            <Form.Control 
                                ref={areaRef}
                                name='entry.642123507' 
                                type="text" 
                                placeholder="SS - DG - UPE" 
                            />
                        </Form.Group>
                    </Col>
                    <Col>
                        <Form.Group controlId="">
                            <Form.Label>Programa (si aplica): </Form.Label>
                            <Form.Control 
                                ref={programaRef}
                                name='entry.368642528' 
                                type="text" 
                                placeholder="ej: Conociendo BA, En la Ciudad, Votás, etc" 
                            />
                        </Form.Group>
                    </Col>
                </Row>
                <hr/>
                <Form.Group>
                    <Form.Label as="legend">
                        Tipo de Publicación
                    </Form.Label>
                    <Form.Check
                        type="radio"
                        label="Flyer Digital*"
                        value="Flyer Digital"
                        name="entry.2142011305"
                        id="formHorizontalRadios1"
                        onChange={(e)=>onValueChange(e)}
                    />
                    <Form.Check
                        type="radio"
                        label="Album de fotos"
                        value="Album de fotos"
                        name="entry.2142011305"
                        id="formHorizontalRadios2"
                        onChange={(e)=>onValueChange(e)}
                    />
                    <Form.Check
                        type="radio"
                        label="Video"
                        value="Video"
                        name="entry.2142011305"
                        id="formHorizontalRadios3"
                        onChange={(e)=>onValueChange(e)}
                    />
                    <Form.Check
                        type="radio"
                        label="Nota"
                        value="Nota"
                        name="entry.2142011305"
                        id="formHorizontalRadios4"
                        onChange={(e)=>onValueChange(e)}
                    />
                    <small 
                        id="datePickerHelp" 
                        className="form-text text-muted"
                    >*Si necesitas publicar un flyer digital, pedilo antes en <Link to='/diseno'>Diseño</Link>.</small>
                </Form.Group>
                <Form.Group controlId="">
                    <Form.Label>Texto para la publicación: </Form.Label>
                    <Form.Control 
                        ref={textoParaPublicacionRef}
                        name='entry.2140385531' 
                        as="textarea" 
                        rows={5}
                        placeholder="EJ:&#10;¡Ahora también te podés casar por la tarde!&#10;En la sede del Registro Civil central, en Uruguay 753, te podés casar entre las 7:30 y 18:00 hs.&#10;Para más información, entrá en: http://bit.ly/RCSedes"
                    />
                </Form.Group>
                <Form.Group>
                    <Form.Label>
                        Fecha de Publicación:
                    </Form.Label>
                    <DatePicker
                        selected={startDate}
                        onChange={date => setStartDate(date)}
                        customInput={<ExampleCustomInput />}
                        minDate={subDays(new Date(), 1)}
                        name='entry.1669131330'
                        dateFormat="dd/MM/yyyy"
                    />
                    <small 
                        id="datePickerHelp" 
                        className="form-text text-muted"
                    >Si queres especificar una franja horario utilizá el campo de observaciones más abajo.</small>
                </Form.Group>
                <Form.Group controlId="">
                    <Form.Label>Para enviarnos imágenes o documentos subilos a GoogleDrive/DropBox y pega el enlace acá: </Form.Label>
                    <Form.Control 
                        ref={urlImagenesRef}
                        name='entry.105901830' 
                        type="text" 
                        placeholder="https://drive.google.com/file/etc.jpg" 
                    />
                    <small 
                        id="datePickerHelp" 
                        className="form-text text-muted"
                    >*Las imagenes deberán tener un tamaño minimo de: 950 X 950px.</small>
                </Form.Group>
                <Form.Group controlId="">
                    <Form.Label>Observaciones: </Form.Label>
                    <Form.Control 
                        ref={observacionesRef}
                        name='entry.1809015096' 
                        as="textarea" 
                        rows={6}
                        placeholder={null} 
                    />
                </Form.Group>
                <Button
                    type='submit'
                >Enviar</Button>
            </Form>
        </Container>
    )
}

export default Redes
