import React from 'react'
import { useHistory, Link, useLocation } from 'react-router-dom'
import { useAuth } from '../context/AppContext'
import { Container, Navbar, Nav, NavDropdown, Button } from 'react-bootstrap'


const JumboNavBar = ({ props }) => {

    const history = useHistory()
    const location = useLocation()
    const { logout } = useAuth()

    const atajos = [
        {atajoTo:'/diseno',
        atajoText: 'Diseño'}, 
        {atajoTo:'/redes',
        atajoText: 'Redes'}, 
        {atajoTo:'/web',
        atajoText: 'Web'}, 
        {atajoTo:'/somos',
        atajoText: 'Somos'}
    ]

    async function handleLogout() {
        try {
            await logout(true)
            history.push('/login')
        } catch {
            console.log('Failed to log out')
        }
    }

    console.log(location.pathname)

    return (
        <Navbar collapseOnSelect variant='dark' expand="lg">
            <Container>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="d-flex justify-content-between w-100">
                        <div class="d-flex">
                            <Nav.Link href="#features">Material Útil</Nav.Link>
                            <Nav.Link href="#pricing">Contacto</Nav.Link>
                            <NavDropdown title='Atajos' id="collasible-nav-dropdown">
                            { atajos.map(atajo=>{
                                if (location.pathname !== '/' && atajo.atajoTo !== location.pathname) {
                                    return (
                                        <Link className='dropdown-item' to={atajo.atajoTo}>
                                            {atajo.atajoText}
                                        </Link>)
                                }
                            })
                            }
                            </NavDropdown>
                        </div>
                        <NavDropdown title={props.toUpperCase()} id="collasible-nav-dropdown">
                            <Link className='dropdown-item' to="profile">Perfil</Link>
                            <Link className='dropdown-item' to="profile">Pedidos</Link>
                            <NavDropdown.Divider />
                            <Button 
                                onClick={handleLogout} 
                                variant='link' 
                                className='dropdown-item'
                            >Cerrar sesión</Button>
                        </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}

export default JumboNavBar
