import React from 'react'
import { Nav, Container } from 'react-bootstrap'

const CoronaAlert = () => {
    return (
        <Nav className='coronaAlert'>
            <Container>
            <a 
                href="https://www.buenosaires.gob.ar/coronavirus" 
                target='_blank'
                rel="noreferrer"
            ><span>Coronavirus (Covid-19):</span> cliqueá para leer toda la información.</a>
            </Container>
        </Nav>
    )
}

export default CoronaAlert
