import React, { useState, useEffect, useRef } from 'react'
import { useHistory } from 'react-router-dom'
import { useAuth } from '../context/AppContext'
import { Container, Form, Button } from 'react-bootstrap'
import axios from 'axios'

const Test = () => {

    const { getBasicInfo } = useAuth()

    const history = useHistory()
    let sended = false
    const nameRef = useRef()
    const emailRef = useRef()
    const areaRef = useRef()
    const programaRef = useRef()

    const handleSubmit = () => {
        getBasicInfo(
            nameRef.current.value, 
            emailRef.current.value, 
            areaRef.current.value, 
            programaRef.current.value
        )
        
        sended = true
    }

    return (
        <Container>
            {/* <iframe 
                name='hidden_iframe' 
                title='hidden_iframe'
                id='hidden_iframe' 
                style={{display:'none'}} 
                onLoad={()=>{
                    if (sended) {
                        history.push('/todobien')
                    }
                }}
            /> */}
            <Form 
                /* action='https://docs.google.com/forms/u/0/d/e/1FAIpQLSdGHugHG6BLHus-upNhHda7LsPit2rLQPEjERce-TBzaMd2EQ/formResponse' */
                
                onSubmit={ (e) => { handleSubmit(e) } }
                target='hidden_iframe'
                className='mb-5'
            >
                <Form.Group controlId="">
                    <Form.Label>nombre</Form.Label>
                    <Form.Control 
                        ref={nameRef}
                        name='entry.83606157' 
                        type="text" 
                        placeholder="" 
                    />
                </Form.Group>
                <Form.Group controlId="">
                    <Form.Label>email</Form.Label>
                    <Form.Control 
                        ref={emailRef}
                        name='entry.1061861810' 
                        type="text" 
                        placeholder="" 
                    />
                </Form.Group>
                <Form.Group controlId="">
                    <Form.Label>area</Form.Label>
                    <Form.Control 
                        ref={areaRef}
                        name='entry.1611122149' 
                        type="text" 
                        placeholder="" 
                    />
                </Form.Group>
                <Form.Group controlId="">
                    <Form.Label>programa</Form.Label>
                    <Form.Control 
                        ref={programaRef}
                        name='entry.358548957' 
                        type="text" 
                        placeholder="" 
                    />
                </Form.Group>

                
                <Button variant="primary" type="submit">
                    Submit
                </Button>
            </Form>
        </Container>
    )
}

export default Test
