import React from 'react'
import { Container } from 'react-bootstrap'

const JumboAreaTitle = ({props}) => {

    return (
        <Container>
            <div className="area-title" style={ !props ? {paddingBottom: '79px'}: {}} >
                <h1>Ministerio de Gobierno</h1>
                <p className="lead">Fortaleciendo el ejercicio pleno de la autonomía hacia la construcción de una cultura de convivencia y gestión responsable, abierta e innovadora entre la Ciudad, la Nación, las Provincias y los Municipios.</p>
            </div>
        </Container>
    )
}

export default JumboAreaTitle
