import React from 'react'
import { Nav, Container } from 'react-bootstrap'

const LogoNav = () => {
    return (
        <Nav className='logoNav'>
            <Container>
                <img 
                    alt="Logo de la ciudad de buenos aires" 
                    src="img/BA2016.png" 
                />
            </Container>
        </Nav>
    )
}

export default LogoNav
