import React, { useRef, useState, forwardRef } from 'react'
import { useHistory } from 'react-router-dom'
import { Container, Form, Row, Col, Button } from 'react-bootstrap'
import DatePicker from "react-datepicker";
import {subDays} from "date-fns";
import { useAuth } from '../context/AppContext'


const Design = () => {

    const { pedidoDisenoSubmited, currentUser } = useAuth()

    const history = useHistory()

    let sended = false
    const nameRef = useRef('')
    const emailRef = useRef('')
    const ccEmailRef = useRef('')
    const areaRef = useRef('')
    const programaRef = useRef('')
    const [ tipoDePieza, setTipodePieza ] = useState('')
    const [startDate, setStartDate] = useState(subDays(new Date(), -10));
    const fechaDeEntregaRef = useRef('')
    const especificacionesRef = useRef('')
    const textoParaLaPiezaRef = useRef('')
    const urlImagenesRef = useRef('')
    const observacionesRef = useRef('')

    const [ showProceso, setShowProceso ] = useState(false)

    
    const ExampleCustomInput = forwardRef(
        ({ value, onClick }, ref) => (
            <input 
                name='entry.1669131330' 
                className="btn btn-default ml-3" 
                onClick={(e)=>onClick()} 
                ref={fechaDeEntregaRef}
                defaultValue={value}
            />
        ),
    );


    return (
        <Container>
            <h1 className='mb-4'>Iniciá tu pedido de diseño</h1>
            <h3> ¿Cómo es el proceso? 
                <button 
                    onClick={()=>setShowProceso(!showProceso)} 
                    /* className="fa fa-plus-square ml-3" */
                    className={ `fa ml-3 fa-${showProceso ? 'minus':'plus'}-square` }
                    /* showProceso ? 'fa fa-minus-square ml-3':'fa fa-plus-square ml-3' */
                    style={{ cursor: 'pointer', padding: '0', border: 'none' }}
                ></button>
            </h3>
            <div 
                style={{ textAlign: 'center', display : showProceso ? 'block' : 'none' }}
                className='mb-5 mt-3'
            >
                <img className='img-fluid' src='/img/diseno-tiempos.png' />
            </div>
            <hr/>
            <iframe 
                name='hidden_iframe' 
                title='hidden_iframe'
                id='hidden_iframe' 
                style={{display:'none'}} 
                onLoad={()=>{
                    if (sended) {
                        const newPedido = {
                            id: currentUser.uid,
                            type: 'Diseño',
                            info: [
                                {text: 'Nombre: ',
                                answer: nameRef.current.value},
                                {text: 'Email: ',
                                answer: emailRef.current.value},
                                {text: 'Email para Copia:',
                                answer: ccEmailRef.current.value},
                                {text: 'Área: ',
                                answer: areaRef.current.value},
                                {text: 'Programa: ',
                                answer: programaRef.current.value},
                                {text: 'Tipo de Pieza: ',
                                answer: tipoDePieza},
                                {text: 'Fecha de Entrega del original: ',
                                answer: fechaDeEntregaRef.current.value},
                                {text: 'Especificaciones: ',
                                answer: especificacionesRef.current.value},
                                {text: 'Url para Imágenes: ',
                                answer: urlImagenesRef.current.value},
                                {text: 'Observaciones: ',
                                answer: observacionesRef.current.value},
                            ]
                        }
                        history.push('/todobien')
                        pedidoDisenoSubmited(newPedido)
                    }
                }}
            />
            <Form
                action='https://docs.google.com/forms/u/1/d/e/1FAIpQLScCaOCmpixdjN4FnOcbWsxF-cxvwxdQxQA_uWCWBlAvxqtyYQ/formResponse'
                onSubmit={ () => sended = true }
                target='hidden_iframe'
                method='POST'
                className='mb-5'
            >
                <Form.Group controlId="email">
                    <Form.Label>Tu nombre: </Form.Label>
                    <Form.Control 
                        ref={nameRef}
                        name='entry.45610068' 
                        type="text" 
                        placeholder="Juan Vecinos" 
                    />
                </Form.Group>
                <Row>
                    <Col>
                        <Form.Group controlId="CCemail">
                            <Form.Label>Correo Electrónico: </Form.Label>
                            <Form.Control 
                                ref={emailRef}
                                name='entry.1601509392' 
                                type="text" 
                                placeholder="tu.nombre@email.com" 
                            />
                        </Form.Group>
                    </Col>
                    <Col>
                        <Form.Group controlId="">
                            <Form.Label>Correo Electrónico en copia: </Form.Label>
                            <Form.Control 
                                ref={ccEmailRef}
                                name='entry.1060413570' 
                                type="text" 
                                placeholder="tu.nombre@email.com, otro.nombre@email.com" 
                            />
                        </Form.Group>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <Form.Group controlId="">
                            <Form.Label>Área: </Form.Label>
                            <Form.Control 
                                ref={areaRef}
                                name='entry.22646789' 
                                type="text" 
                                placeholder="SS - DG - UPE" 
                            />
                        </Form.Group>
                    </Col>
                    <Col>
                        <Form.Group controlId="">
                            <Form.Label>Programa (si aplica): </Form.Label>
                            <Form.Control 
                                ref={programaRef}
                                name='entry.1334255680' 
                                type="text" 
                                placeholder="ej: Conociendo BA, En la Ciudad, Votás, etc" 
                            />
                        </Form.Group>
                    </Col>
                </Row>
                <hr/>
                <Form.Group>
                    <Form.Label as="legend">
                        Tipo de Pieza
                    </Form.Label>
                    <Form.Check
                        type="radio"
                        label="Pieza Digital"
                        value="Pieza Digital"
                        name="entry.1658896877"
                        id="formHorizontalRadios1"
                        onChange={(e)=>setTipodePieza(e.target.value)}
                    />
                    <Form.Check
                        type="radio"
                        label="Pieza Impresa"
                        value="Pieza Impresa"
                        name="entry.1658896877"
                        id="formHorizontalRadios2"
                        onChange={(e)=>setTipodePieza(e.target.value)}
                    />
                    <Form.Check
                        type="radio"
                        label="Ambas"
                        value="Ambas"
                        name="entry.1658896877"
                        id="formHorizontalRadios3"
                        onChange={(e)=>setTipodePieza(e.target.value)}
                    />
                </Form.Group>
                <Form.Group>
                    <Form.Label>
                        Fecha de Entrega del Original:
                    </Form.Label>
                    <DatePicker
                        selected={startDate}
                        onChange={date => setStartDate(date)}
                        minDate={subDays(new Date(), -10)}
                        placeholderText={subDays(new Date(), -10)}
                        customInput={<ExampleCustomInput />}
                        startDate={subDays(startDate, -10)}
                        name='entry.1669131330'
                        dateFormat="dd/MM/yyyy"
                    />
                    <small 
                        id="datePickerHelp" 
                        className="form-text text-muted"
                    >*Si tenes un pedido urgente y la fecha más próxima posible es posterior, por favor dejanos una aclaración utilizando el campo "Observaciones".</small>
                </Form.Group>
                <Form.Group controlId="">
                    <Form.Label>Especificaciones: </Form.Label>
                    <Form.Control 
                        ref={especificacionesRef}
                        name='entry.1806980425' 
                        as="textarea" 
                        rows={3}
                        placeholder="Si elegiste digital, ejs: pieza para redes, mailing, firma para mail, etc&#10;Si elegiste impresa, ejs: Folleto doble faz, libro" 
                    />
                </Form.Group>
                <Form.Group controlId="">
                    <Form.Label>Texto para la pieza: </Form.Label>
                    <Form.Control 
                        ref={textoParaLaPiezaRef}
                        name='entry.1751853484' 
                        as="textarea" 
                        rows={6}
                        placeholder="Texto a situarse dentro de la pieza" 
                    />
                    <small 
                        id="datePickerHelp" 
                        className="form-text text-muted"
                    >*Si el texto tiene jerarquización o es muy largo envialo como documento a continuación.</small>
                </Form.Group>
                <Form.Group controlId="">
                    <Form.Label>Para enviarnos imágenes o documentos subilos a GoogleDrive/DropBox y pega el enlace acá: </Form.Label>
                    <Form.Control 
                        ref={urlImagenesRef}
                        name='entry.258070727' 
                        type="text" 
                        placeholder="https://drive.google.com/file/etc.jpg" 
                    />
                </Form.Group>
                <Form.Group controlId="">
                    <Form.Label>Observaciones: </Form.Label>
                    <Form.Control 
                        ref={observacionesRef}
                        name='entry.1641688495' 
                        as="textarea" 
                        rows={6}
                        placeholder={null} 
                    />
                </Form.Group>
                <Button
                    type='submit'
                >Enviar</Button>
            </Form>
        </Container>
    )
}

export default Design
