import React from 'react'
import { useLocation, Link } from 'react-router-dom'
import { Container, Breadcrumb } from 'react-bootstrap'

const JumboBreadcrumbs = () => {

    const { pathname } = useLocation()
    const breadPath = pathname.replace('/', '')
    const home = '/'

    return (
        <Breadcrumb>
            <Container>
                <li className='breadcrumb-item'>
                    <Link to="/">Inicio</Link>
                </li>
                {pathname === home ? 
                    null
                    : <Breadcrumb.Item 
                        style={{ color: 'rgb(204, 204, 204)'}}
                        active 
                        href="#"
                    >{breadPath}</Breadcrumb.Item>
                }
                
            </Container>
        </Breadcrumb>
    )
}

export default JumboBreadcrumbs
