import React from 'react'
import { Col, Row } from 'react-bootstrap'

const TableRTwo = ({props}) => {

    const { key, value } = props
    console.log(props)

    return (
        <>
            { key !== 'Url para Imágenes: ' && key !== 'Email: ' ? 
                <Row>
                    <Col sm={6} md={3}>
                        {key}
                    </Col>
                    <Col sm={6} md={3}>
                        {value}
                    </Col>
                </Row>
                : <Row>
                    <Col sm={6} md={3}>
                        {key}
                    </Col>
                    <Col sm={6} md={3}>
                        <a href={value}>{value}</a>
                    </Col>
                </Row> }
        </>
    )
}

export default TableRTwo
