import React, { createContext, useContext, useState, useEffect } from 'react'
import { auth, getFirestore } from '../firebase'

const AuthContext = createContext()

const { Provider } = AuthContext

export const useAuth = () => {
    return useContext(AuthContext)
}

export const AuthProvider = ({ children }) => {

    const [ currentUser, setCurrentUser ] = useState()
    const [ loading, setLoading ] = useState(true)


    /* SING UP FUNCTION */
    function signup( email, password ) {
        return auth.createUserWithEmailAndPassword( email, password )
    }

    /* LOGIN FUNCTION */
    function login( email, password ) {
        return auth.signInWithEmailAndPassword( email, password )
    }

    /* LOG OUT FUNCTION */
    function logout() {
        setReferentes([])
        setLoadingRefentes(true)
        setReferentesExist(false)
        return auth.signOut()
    }

    /* UPDATE FUNCTION */
    function update(displayName) {
        const user = auth.currentUser
        return user.updateProfile({
            displayName
        })
    }

    /* CHECKS IF LOGED IN */
    useEffect(() => {
        const unsubscribe = auth.onAuthStateChanged(user => {
            setCurrentUser(user)
            setLoading(false)
        })
        return unsubscribe
    }, [])

    /* GETS REFERENTES IF ANY */
    
    const [ referentes, setReferentes ] = useState([])
    const [ loadingReferentes, setLoadingRefentes] = useState(true)
    const [ referenteExist, setReferentesExist ] = useState(false)

    const getReferentes = () => {
        const db = getFirestore()
        const referentesCollection = db.collection('referentes')
        if (currentUser) {
            const query = referentesCollection.where('areaid', '==', currentUser.uid)
            query.get()
            .then((querySnapshop)=>{
                if (querySnapshop.docs[0]) {
                    querySnapshop.forEach((doc)=>{
                        setReferentes(referentes => [...referentes, doc.data()])
                        //console.log('referente existe =>', doc.data()  )
                    })
                    setLoadingRefentes(false)
                    setReferentesExist(true)
                } else {
                    setLoadingRefentes(false)
                    setReferentesExist(false)
                    //console.log('referente no existe =>', referentes  )
                }
            })
        }
    }
    

    /* FORM HANDLER */
    
    const getBasicInfo = (nameRef, emailRef, areaRef, programaRef) => {

        const newUser = {
            nameRef, 
            emailRef, 
            areaRef, 
            programaRef
        }
        console.log(newUser)
        /* setNombre()
        setEmail()
        setArea()
        setPrograma() */
    }
    const [ newRequest, setNewRequest] = useState()

    const pedidoDisenoSubmited = (newPedido) =>  {
        console.log(newPedido)
        setNewRequest(newPedido)
    }

    const pedidoRedesSubmited = (newPedido) => {
        console.log(newPedido)
        setNewRequest(newPedido)
    }


    /* CONTEXT IMPORTS */
    const value = {
        currentUser,
        login,
        signup,
        logout,
        update,
        getReferentes,
        loadingReferentes,
        referenteExist,
        referentes,
        getBasicInfo,
        pedidoDisenoSubmited,
        pedidoRedesSubmited,
        newRequest
    }

    return (
        <Provider value={value}>
            {!loading && children}
        </Provider>
    )
}