import React, { useRef, useState } from 'react'
import { useAuth } from '../context/AppContext'
import { useHistory } from 'react-router-dom'
import { Container, Card, Form, Button, Alert } from 'react-bootstrap'

const Login = () => {

    const emailRef = useRef()
    const passwordRef = useRef()
    const { login } = useAuth()
    const [ error, setError ] = useState('')
    const [ loading, setLoading ] = useState(false)
    const history = useHistory()

    async function handleSubmit(e) {
        e.preventDefault()
        try {
            setError("")
            setLoading(true)
            const createEmail = emailRef.current.value + '@pediloya.club'
            await login(createEmail, passwordRef.current.value)
            history.push('/')
        } catch {
            setError("Failed to sign in")
        }
        setLoading(false)
    }

    return (
        <Container className='d-flex align-items-center flex-column'>
            <h3 className='mb-5'>Plataforma Interna del equipo de Comunicación</h3>
            <div className='w-100' style={{ maxWidth: '400px' }}>
                <Card className='mb-5'>
                    <Card.Header 
                        style={{ fontSize : '1.22rem', fontWeight : '700' }}
                    >Ingresar</Card.Header>
                    <Card.Body>
                        {error && <Alert variant='danger'>{error}</Alert>}
                        <Form onSubmit={handleSubmit}>
                            <Form.Group id='email'>
                                <Form.Label>Email</Form.Label>
                                <Form.Control type='text' ref={emailRef} required />
                            </Form.Group>
                            <Form.Group id='password'>
                                <Form.Label>Contraseña</Form.Label>
                                <Form.Control type='password' ref={passwordRef} required />
                            </Form.Group>
                            <Button 
                                disabled={loading} 
                                className='w-100' 
                                type='submit'
                                variant='primary'
                            >
                                Vamos!
                            </Button>
                        </Form>
                    </Card.Body>
                    
                </Card>
            </div>
        </Container>
    )
}

export default Login
