import React from 'react'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
//CONTEXT
import { AuthProvider } from '../context/AppContext';
//COMPONENTS
import PrivateRoute from './PrivateRoute'
import Login from './Login'
import Main from './Main'
import Header from './Header';
import AppFooter from './AppFooter'
import Design from './Design'
import Redes from './Redes'
/* TEST COMPONENTS */
import Test from './Test'
import TodoBien from './TodoBien'
import TestDesign from './TestDesign'
//CSS
import './main.css'
import "react-datepicker/dist/react-datepicker.css";


function App() {
  return (
    <Router>
      <AuthProvider>
        <Header />
        <Switch>
          <PrivateRoute exact path='/' component={ Main } />
          <PrivateRoute exact path='/test' component={ Test } />
          <PrivateRoute exact path='/todobien' component={ TodoBien } />
          <PrivateRoute exact path='/diseno' component={ Design } />
          <PrivateRoute exact path='/redes' component={ Redes } />
          <PrivateRoute exact path='/testdiseno' component={ TestDesign } />
          <Route path='/login' component={ Login } />
        </Switch>
        <AppFooter />
      </AuthProvider>
    </Router>
  );
}

export default App;
