import React from 'react'
import { useAuth } from '../context/AppContext'
import { Container } from 'react-bootstrap'
import TableRTwo from './TableRTwo'

const TodoBien = () => {
    const { newRequest } = useAuth()
    const info = newRequest ? newRequest.info : null
    console.log(newRequest)
    return (
        <Container className='todobien mb-5'>
            <h3>Tu pedido:</h3>
            { info ? 
                
                info.map((each)=>{
                    return <TableRTwo props={{key: each.text, value: each.answer}} />
                })
                /* newRequest.info.map(i=>{
                    <TableRTwo props={
                        { key: i.text, value: i.answer}
                    }/>
                }) */
                : <p>No hay ningún pedido registrado</p>
            }
            
        </Container>
    )
}

export default TodoBien
