import React from 'react'
//CONTEXT
import { useAuth } from '../context/AppContext';
import { Jumbotron } from 'react-bootstrap'
import JumboBreadcrumbs from './JumboBreadcrumbs'
import JumboAreaTitle from './JumboAreaTitle'
import JumboNavBar from './JumboNavBar'

const JumboNav = () => {

    const { currentUser } = useAuth()

    return (
        <Jumbotron className='minGobJumbo'>
            <div className='minGobJumboOverlay'>
                <JumboBreadcrumbs />
                {currentUser ? 
                    <JumboAreaTitle props={ currentUser.displayName } />
                    : <JumboAreaTitle props={ null } />
                }
                { currentUser ? <JumboNavBar props={ currentUser.displayName } /> : null }
            </div>
        </Jumbotron>
    )
}

export default JumboNav
